body {
  background: url("../../../build/img/pattern.png");
}

.wrap {
  width: 1200px;
  margin: 0px auto;
}

.icon {
  font-family: "WebSymbols";
  margin-top: 0px;
}

.clearfix {
  float: none;
  clear: both;
}

@font-face {
  font-family: "Trebuchet";
  src: url("../../../build/css/fonts/TrebuchetMS.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "BebasNeue";
  src: url("../../../build/css/fonts/BebasNeue.otf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "WebSymbols";
  src: url("../../../build/css/fonts/websymbols-regular-webfont.svg#WebSymbols") format("svg");
  src: url("../../../build/css/fonts/websymbols-regular-webfont.eot"), url("../../../build/css/fonts/websymbols-regular-webfont.eot?#iefix"), url("../../../build/css/fonts/websymbols-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
/*Colors*/
header {
  width: 100%;
  height: 70px;
  background-color: #000;
  font-family: "BebasNeue";
  color: #fff;
}
header #logo {
  float: left;
  width: 200px;
  height: 55px;
  margin-top: 8px;
  margin-left: 20px;
  background-color: #37bcf9;
  text-align: center;
  letter-spacing: 1px;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s;
}
header #logo .gear {
  font-family: "WebSymbols";
  display: block;
  float: left;
  font-size: 30px;
  margin-top: 8px;
  margin-left: 26px;
}
header #logo h3 {
  display: block;
  font-size: 40px;
  float: right;
  margin-top: 5px;
  margin-right: 65px;
  transition: all 0.3s;
}
header nav ul {
  float: right;
  height: 70px;
  margin-top: 0px;
  padding-left: 0px;
  list-style: none;
}
header nav ul li {
  font-size: 24px;
  display: inline-block;
  margin: 0px 20px;
  letter-spacing: 1px;
  line-height: 70px;
}
header nav ul li a {
  display: block;
  color: #fff;
  text-decoration: none;
  transition: all 0.2s;
}
header nav ul li a:hover {
  color: #37bcf9;
  transform: scale(1.2, 1.2);
}

#info #banner {
  width: 95%;
  height: 100px;
  border: 10px solid #fff;
  box-shadow: 0px 0px 2px gray;
  background: url("../../../build/img/bakbaner.png");
  background-position: center right;
  margin: 20px auto;
}
#info #banner h1 {
  display: block;
  width: 850px;
  font-family: "BebasNeue";
  color: white;
  font-size: 45px;
  letter-spacing: 2px;
  text-shadow: 2px 2px 1px #000;
  text-align: center;
  margin: 27px auto;
}

#cards .card {
  float: left;
  width: 205px;
  height: 305px;
  background: #fff;
  overflow: hidden;
  margin: 40px 16px 20px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 2px #ddd;
  cursor: pointer;
}
#cards .card .icon {
  display: block;
  width: 100%;
  height: 130px;
  font-size: 50px;
  line-height: 130px;
  text-align: center;
  margin: 0px auto;
}
#cards .card .category {
  width: 100%;
  height: 50px;
  font-family: "Trebuchet";
  font-size: 21px;
  font-weight: bold;
  text-align: center;
  color: #00538c;
  margin-bottom: 0px;
}
#cards .card .description {
  margin: 0px 10px;
  text-align: center;
  color: #0677c2;
  font-family: "Trebuchet";
}

#aside {
  width: 300px;
  min-height: 1200px;
  font-family: "Trebuchet";
  margin: 20px;
  float: right;
}
#aside h3 {
  display: block;
  width: auto;
  height: 45px;
  font-family: "BebasNeue";
  font-size: 30px;
  font-weight: normal;
  letter-spacing: 2px;
  line-height: 50px;
  text-transform: uppercase;
  box-shadow: 0px 1px 0px #393d3e, 1px 2px 0px #393d3e, 2px 3px 0px #393d3e, 3px 4px 0px #393d3e;
  background: url("../../../build/img/pxgray.png");
  padding-left: 15px;
  margin-top: 30px;
  margin-bottom: 15px;
}
#aside h3:first-child {
  margin-top: 0px;
}
#aside #search {
  width: 90%;
  height: 30px;
  margin: 5px auto;
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 4px #ccc inset;
}
#aside #search button {
  height: 30px;
  font-size: 16px;
  padding-left: 12px;
  background-color: transparent;
  border: none;
  border-left: 1px solid #ccc;
  color: #555;
  text-align: center;
  cursor: pointer;
}
#aside .aside-box {
  width: 85%;
  height: 240px;
  margin: 20px auto 0px;
  padding-top: 2px;
  background: #fff;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.3), 0px 3px 7px rgba(0, 0, 0, 0.3), 0px 1px #fff inset, 0px -3px 2px rgba(0, 0, 0, 0.3) inset;
}
#aside input[type=text] {
  width: 80%;
  height: 25px;
  border: none;
  padding-left: 5px;
  border-radius: 5px;
  background-color: transparent;
  color: #ccc;
  transition: all 0.3s;
}
#aside input[type=text]:focus {
  color: #555;
  outline: none;
}
#aside #login input[type=email], #aside #login input[type=password] {
  display: block;
  margin: 20px auto 0px;
  width: 80%;
  height: 30px;
  border: 1px solid #ccc;
  padding-left: 35px;
  box-shadow: 1px 1px 1px #ccc inset;
  border-radius: 5px;
}
#aside #login input[type=email]:focus, #aside #login input[type=password]:focus {
  border: none;
  color: #555;
  box-shadow: 1px 1px 1px gray inset;
}
#aside #login #user, #aside #login #password {
  display: block;
  text-align: center;
  position: absolute;
}
#aside #login #user {
  margin-left: 15px;
  margin-top: 8px;
  height: 30px;
}
#aside #login #password {
  margin-top: 30px;
  margin-left: 18px;
  height: 30px;
}
#aside #login button {
  text-align: center;
  width: 100px;
  height: 35px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 16px;
  font-size: 15px;
  font-family: "Trebuchet";
  font-weight: lighter;
  color: #fff;
  background: linear-gradient(to bottom, #3eb8e5 0%, #2ca0ca 100%);
  border: 1px solid #156785;
  cursor: pointer;
  transition: all 0.3s;
}
#aside #login button:hover {
  box-shadow: 0px 0px 3px gray;
}
#aside #login a {
  display: block;
  text-decoration: none;
  margin-left: 18px;
  font-size: 14px;
  color: #555;
}
#aside #login a:hover {
  text-decoration: undeline;
  color: #2f78bc;
}
#aside #login a:last-child {
  margin-top: 5px;
}
#aside #social {
  height: 200px;
}
#aside #social a {
  display: block;
  text-decoration: none;
  text-align: center;
  font-size: 30px;
  margin-top: 20px;
  color: #333;
}

section #articles {
  float: left;
  width: 860px;
}
section #articles h2 {
  display: block;
  width: 100%;
  height: 75px;
  line-height: 75px;
  text-align: center;
  font-family: "BebasNeue";
  font-size: 40px;
  letter-spacing: 2px;
  background: url("../../../build/img/pxgray.png"), #fff;
}